* {
    margin: 0;
    padding: 0;
}

body {
    background-color: #f9f2ed;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-family: raleway;

    @media (max-width:680px) {
        padding: 20px;
    }
}

.wrapper {
    width: 600px;
    min-height: 50px;
    display: flex;
    justify-content: space-between;
    background: white;
    padding: 20px;

    @media (max-width:680px) {
        flex-direction: column;
        width: 100%;
        padding: 5px;
    }
}

.block {
    width: 48%;

    @media (max-width:680px) {
        width: 90%;
        margin: 15px auto 15px auto;

    }

    &__rates {
        margin-bottom: 20px;

    }


    &__input {}
}

.rates-block {

    &__list {
        display: flex;
        list-style-type: none;

    }

    &__item {
        width: 28.888%;
        display: flex;
        align-items: center;
        justify-content: center;
        border: rgba(0, 0, 0, 0.1) solid 1px;
        border-right: none;
        text-transform: uppercase;
        font-weight: 400;
        padding: 8px 8px;
        font-size: 13px;

    }

    &__item:last-child {
        border: rgba(0, 0, 0, 0.1) solid 1px;
        position: relative;
        width: 15%;

        &:after {
            display: block;
            content: ' ';
            position: absolute;
            top: 50%;
            left: 50%;
            border-right: 1px solid #000;
            border-top: 1px solid #000;
            transform: rotate(135deg) translateY(80%);
            width: 5px;
            height: 5px;
        }
    }

    &__item:first-child {
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
    }

    &__item:last-child {
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
    }
}

.input-block {

    &__item {
        border: 1px solid rgba(0, 0, 0, 0.1);
        padding: 10px;
        outline: none;
        font-weight: 500;
        width: 100%;
        height: 65px;
        font-size: 24px;
        border-radius: 5px;
        box-sizing: border-box;

    }
}

.values {
    position: relative;



    &__list {
        position: absolute;
        background: white;
        box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.1);
        top: 38px;
        z-index: 1000;
        width: 100%;
        max-height: 200px;
        overflow: scroll;
        text-align: center;
        border-radius: 5px;
        display: none;
    }

    &__item {
        list-style-type: none;
        padding: 10px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }

    &__input {

        outline: none;
        width: 85%;
    }
}

.active {
    border: none;
    background-color: #16b67f;
    color: white;
}

.block {
    display: block;
}